const getFavs = async (token) => {
    try {

        const header = {
            method: 'GET',
            headers: { "Authorization": `Bearer ${token}` }
        }

        const postsResponse = await fetch(
            "https://posts-pw2021.herokuapp.com/api/v1/post/fav/",
            header
        )


        if (!(postsResponse.ok)) {

            throw new Error()
        }
        const data = await postsResponse.json()

        return data

    } catch (error) {
        return ({ "favorites": [] })

    }
}

export default getFavs