import PaginaInicio from "./pages/home.js"
import MisPaginas from "./pages/mine.js"
import Favoritos from "./pages/favs"
import { Routes, Route, BrowserRouter, Navigate } from 'react-router-dom';
import Publish from "./pages/publish.js";
import { useUserContext } from "./contexts/UserContext";
import Login from "./pages/Login/Login.js";
import LogOut from "./pages/logout.js";




function App() {
  const { token, user } = useUserContext();

  const PrivateRute = ({ rute }) => {

    if (!token) {
      return (
        <Navigate replace to="/COMUNIDADUCAGRUPO25/login"></Navigate>
      )
    }

    if (!user) {
      window.location.reload()
    }

    switch (rute) {
      case "home":
          return (
            <PaginaInicio></PaginaInicio>
          )

      case "create":
        return (
          <Publish></Publish>
        )

      case "favs":
        return (
          <Favoritos></Favoritos>
        )

      case "mine":
        return (
          <MisPaginas></MisPaginas>
        )

      case "logout":
        return (
          <LogOut></LogOut>
        )

      default:
        break;
    }
  }

  return (
    <BrowserRouter>
      <Routes>
        <Route path="/COMUNIDADUCAGRUPO25" element={<Navigate replace to="/COMUNIDADUCAGRUPO25/login" />} />
        <Route path="/COMUNIDADUCAGRUPO25/login" element={<Login />} />
        <Route path="/COMUNIDADUCAGRUPO25/home" element={<PrivateRute rute="home" />} />
        <Route path="/COMUNIDADUCAGRUPO25/create" element={<PrivateRute rute="create" />} />
        <Route path="/COMUNIDADUCAGRUPO25/favs" element={<PrivateRute rute="favs" />} />
        <Route path="/COMUNIDADUCAGRUPO25/mine" element={<PrivateRute rute="mine" />} />
        <Route path="/COMUNIDADUCAGRUPO25/logout" element={<PrivateRute rute="logout" />} />
        <Route path="/COMUNIDADUCAGRUPO25/*" element={<Navigate replace to="/COMUNIDADUCAGRUPO25/login" />} />
        <Route path="*" element={<Navigate replace to="/COMUNIDADUCAGRUPO25/login" />} />
        
      </Routes>

    </BrowserRouter>
  );


}

export default App;
