import styles from '../Displayer.module.css'
import React from 'react'

function Comment({user, description}) {
    return (
        <div className = {styles.comment}>
            <div className = {styles.user}>
                <p> {(() => {
                        let indexOf_ = user.username.indexOf("_")
                        let substring = user.username.substring(2, indexOf_)
                        return `Grupo ${substring}`
                    })()} </p>
            </div>
            <div className = {styles.text}>
                <p>
                {description}
                </p>
            </div>
        </div>
    )
}

export default Comment
