import React, { useState, useEffect } from 'react'
import styles from './Displayer.module.css'
import Comment from './comments/Comment'
import SingleCard from "../Cards/singleCard"
import CommentBox from './comments/commentBox'
import getPostById from '../../services/getPostById'
import animation from "./animation.module.css"
import empty from "./empty.png"
import comment from "../../services/comment"
import createPost from "../../services/post"
import { useForm } from 'react-hook-form'
import { FiAlertTriangle } from 'react-icons/fi'
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { useUserContext } from '../../contexts/UserContext'


const schemaCreate = yup.object({
    title: yup.string().min(8).max(32).required(),
    description: yup.string().min(8).required(),
    image: yup.string().url().required()
}).required();

const schemaEdit = yup.object({
    title: yup.string().min(8).max(32),
    description: yup.string().min(8),
    image: yup.string().url()
})

const Displayer = ({ updateData, display }) => {
    
    const { user } = useUserContext()

    const { register: registerCreate, handleSubmit: handleSubmitCreate, formState: { errors:errorCreate } 
    } = useForm({
        resolver: yupResolver(schemaCreate)
    });

    const { register: registerEdit, handleSubmit: handleSubmitEdit, formState: { errors:errorEdit } 
    } = useForm({
        resolver: yupResolver(schemaEdit)
    });

    const [comments, setComments] = useState([])
    const [editData, setEditData] = useState([])
    const [history, setHistory] = useState({
        user: {},
        history: []
    })


    useEffect(() => {
        (async () => {

            const token = localStorage.getItem("token")

            if (display.show === "comment") {
                const { comments } = await getPostById(token, display.data.id)
                setComments(comments)

            }
            else if (display.show === "history") {
                const { user, history } = await getPostById(token, display.data.id)
                if (user && history) {

                    setHistory(
                        {
                            user: user,
                            history: history
                        })
                }
            }
            else if (display.show === "edit") {
                const { title, description, image } = await getPostById(token, display.data.id)
                if(title)
                    setEditData([title, description, image])
                else
                    setEditData([undefined, undefined, undefined])
            }
            else if (display.show === "create") {
                setEditData(["Pon un titulo", "Que estas pensando?", "Una imagen para acompañar !!"])
            }

        })()
    }, [display])

    const postComment = (e) => {
        e.preventDefault()
        comment(display.data.id, e.target[0].value)
        let newComment =  {
            "description": e.target[0].value,
            "user": {
              "username": user.username
            },
            "_id": "618b50c35f980a8d86c5332a"
          }
        setComments(comments.concat([newComment]))
        e.target.reset()
    }

    const ShowComments = () => {

        return (

            <div className={styles.commentContainer}>
                <div className={styles.commentBoxContainer}>
                    <div>
                        <h2 className={styles.padding}>
                            Comentarios
                        </h2>
                    </div>

                    <div className={styles.commentBox}>
                        <CommentBox submit={postComment} />
                    </div>
                </div>

                <div className={styles.comments}>
                    {
                        (() => {
                            if (comments.length) {
                                return (
                                    comments.map(({ _id, user, description }) => {
                                        return <Comment key={_id} user={user} description={description} />

                                    })
                                )
                            } else {
                                return <p className={styles.noComments}> No hay comentarios </p>
                            }

                        })()
                    }
                    { }

                </div>


            </div>

        );
    }

    const EditModeComponent = ({ id, banner, buttonBanner, title, description, image }) => {

        const onSubmit = ({title, description, image}) => {
            if(image)
                updateData(id, title, description, image)
            else
                updateData(id, title, description, editData[2])

            
        }

        const onChange = (e) => {
            updateData(e.target.name, e.target.value)
        }

        const onChangeImg = (e) => {
            setEditData([editData[0], editData[1], e.target.value])
        }

        return (
            <div className={styles.commentContainer}>
                <div className={styles.commentBoxContainer}>
                    <h2 className={styles.padding}>
                        {banner}
                    </h2>
                </div>

                <div className={styles.editForms}>
                    <div>
                        <form className={styles.formColumm} onSubmit={handleSubmitEdit(onSubmit)}>
                            <input className={styles.submitsChanges} value={buttonBanner} type="submit" />

                            <textarea onChange={onChange}  {...registerEdit("title")}
                                className={styles.boldTextArea} value={title} type="text" placeholder={title} />

                            {errorEdit.title && <p className={styles.alert}><FiAlertTriangle />{` ${errorEdit.title.message}`}</p>}

                            <textarea onChange={onChange}  {...registerEdit("description")}
                                className={styles.textareaEdit} value={description} type="text" placeholder={description} />

                            {errorEdit.description && <p className={styles.alert}><FiAlertTriangle />{` ${errorEdit.description.message}`}</p>}

                            <textarea onKeyUp={onChangeImg} {...registerEdit("image")}
                                className={styles.textareaEdit} value={image} type="text" placeholder={image} />

                            {errorEdit.image && <p className={styles.alert}><FiAlertTriangle />{` ${errorEdit.image.message}`}</p>}

                            <div className={styles.image}>
                                <img alt="" src={image} />
                            </div>

                        </form>
                    </div>
                </div>

            </div>
        )
    }

    const CreateModeComponent = ({ id, banner, buttonBanner, title, description, image }) => {

        const onSubmit = ({title, description, image}) => {
                setEditData([title, description, image])
                updateData("title", title)
                updateData("description", description)
                updateData("image", image)
                updateData("update", undefined)
                createPost(title, description, image)

        }

        const onChange = (e) => {
            updateData(e.target.name, e.target.value)
            //setEditData([editData[0], editData[1], e.target.value])
        }

        const onChangeImg = (e) => {
            //updateData(e.target.name, e.target.value)
            setEditData([editData[0], editData[1], e.target.value])
        }

        return (
            <div className={styles.commentContainer}>
                <div className={styles.commentBoxContainer}>
                    <h2 className={styles.padding}>
                        {banner}
                    </h2>
                </div>

                <div className={styles.editForms}>
                    <div>
                        <form className={styles.formColumm} onSubmit={handleSubmitCreate(onSubmit)}>
                            <input className={styles.submitsChanges} value={buttonBanner} type="submit" />

                            <textarea onChange={onChange}  {...registerCreate("title")}
                                className={styles.boldTextArea} type="text" placeholder={title} />

                            {errorCreate.title && <p className={styles.alert}><FiAlertTriangle />{` ${errorCreate.title.message}`}</p>}

                            <textarea onChange={onChange}  {...registerCreate("description")}
                                className={styles.textareaEdit} type="text" placeholder={description} />

                            {errorCreate.description && <p className={styles.alert}><FiAlertTriangle />{` ${errorCreate.description.message}`}</p>}

                            <textarea onKeyUp={onChangeImg} {...registerCreate("image")}
                                className={styles.textareaEdit} type="text" placeholder={image} />

                            {errorCreate.image && <p className={styles.alert}><FiAlertTriangle />{` ${errorCreate.image.message}`}</p>}

                            <div className={styles.image}>
                                <img alt="INGRESA UNA URL VALIDA" src={image} />
                            </div>

                        </form>
                    </div>
                </div>

            </div>
        )
    }

    const HistoryModeComponent = () => {

        console.log(history)
        return (
            <div className={styles.commentContainer}>
                <div className={`${styles.marginButtom} ${styles.commentBoxContainer}`}>
                    <h2 className={styles.padding}>
                        Estas son las versiones anteriores de la publicación
                    </h2>
                </div>
                {

                    history.history.map(({ description, title, image, modifiedAt }) => {
                        return (
                            <SingleCard
                                user={history.user}
                                title={title}
                                description={description}
                                image={image}
                                time={modifiedAt}
                                likes={undefined}
                                comments={undefined}
                            />
                        )
                    })

                }
            </div>
        )
    }

    return (
        <div className={`${styles.container}`}>
            <div className={`${animation["loading-" + display.show]} ${animation[editData[0]]}`}></div>
            {
                (!display.show) && <img className={styles.empty} alt="" src={empty} />
            }
            {
                (() => {
                    if (display.show === "comment")
                        return <ShowComments />
                    else if (display.show === "edit")
                        return (
                            <EditModeComponent
                                banner="Edit tu publicación"
                                buttonBanner="Cambiar datos"
                                id={display.data.id}
                                title={editData[0]}
                                description={editData[1]} image={editData[2]}
                            />);

                    else if (display.show === "create")
                        return (
                            <CreateModeComponent
                                banner="Crea una nueva publicación"
                                buttonBanner="Publicar"
                                title={editData[0]}
                                description={editData[1]}
                                image={editData[2]}
                            />);

                    else if (display.show === "history")
                        return <HistoryModeComponent />
                })()
            }
        </div>
    )
}
export default Displayer



