import React, { useState } from 'react'
import styles from '../App.module.css';
import AppHeader from "../components/Header"
import Cards from "../components/Cards"
import Displayer from "../components/Displayer"
import { IoArrowBackCircle } from 'react-icons/io5'
import { useUserContext } from '../contexts/UserContext';

const Main = () => {

  const CARDSMODE = "user"
  const { width } = useUserContext()
  const [displayerState, changeDisplayer] = useState({
    show: "",
    data: ""
  })

  const historyInfo = (e) => {
    if (e.target.name) {

      changeDisplayer({
        show: "history",
        data: {
          id: e.target.name
        }
      })
    }
    else {
      console.log(e.currentTarget.name)
      changeDisplayer({
        show: "history",
        data: {
          id: e.currentTarget.name
        }
      })
    }
  }

  const commentInfo = (e) => {
    changeDisplayer({
      show: "comment",
      data: {
        id: e.target.name
      }
    })
  }

  const BrowserViewComponents = () => {

    if (width >= 1024 && !((width < window.innerHeight)))
      return (
        <div className={styles.container}>
          <Displayer display={displayerState} />

          <Cards
            mode={CARDSMODE}
            historyButton={historyInfo}
            commentButton={commentInfo}

          />

        </div>)
      else
        return (
          <></>
        )

  }
  const MobileViewComponents = () => {
    if (width < 1024  || (width < window.innerHeight)) {
      if ((displayerState.show))
        return (
        <div className={styles.mobileDisplay}>
            <button className={styles.closeButton} onClick={() => {
              changeDisplayer({
                show: "",
                data: ""
              })
            }}
            >
              <IoArrowBackCircle />
              <span>Regresar</span>
            </button>
            <Displayer display={displayerState} />
          </div>
        )
      else {
        return (

          <div className={styles.cardMobile}>

            <h2 className={styles.title}><p>Publicaciones de la comunidad</p></h2>

            <Cards
              mode={CARDSMODE}
              historyButton={historyInfo}
              commentButton={commentInfo}
              
              />
          </div>
        )

      }
    }
    else
      return (
        <></>
      )
  }

  return (
    <div className={styles.main}>

      <AppHeader headerOf="home" />

      <div className={styles.container}>
        {
          <MobileViewComponents />
          
        }
        {
          <BrowserViewComponents />
        }
      </div>

    </div>
  )
}

export default Main
