const post = async (title, descripcion, image) => {

    let token = localStorage.getItem("token");

    try {
        const header = {
            method: 'POST',
            headers: {
                "Authorization": `Bearer ${token}`,
                "Content-type": "application/json"
            },
            body: JSON.stringify({
                title: title,
                description: descripcion,
                image: image
            })
        }

        const post = await fetch(
            "https://posts-pw2021.herokuapp.com/api/v1/post/create",
            header
            )

        if(!(post.ok)){
            throw new Error()
        }

        //let post = await post.json()

        return true


    } catch (error) {

        console.log(error)

        return false
    }
}

export default post