import { Link } from "react-router-dom";
import styles from "./header.module.css";
import animations from "./animations.module.css";


const HeaderButton = ({active, icon, id, text, route, click }) => {

    let styleCurrent = "";
    let animationCurrent = "";

    if(active===route){
        styleCurrent = `${styles[`border_${route}`]}`
        animationCurrent = `${animations.centerAppear} ${styles.headerButton} ${styles[`${route}`]}`
    }
    else {
        animationCurrent = `${styles.headerButton}  ${styles[`${route}`]}`

    }

    return (
        <div className = {`${styleCurrent}`}> 
            <Link to={`/COMUNIDADUCAGRUPO25/${route}`}>
                <button className = {`${animationCurrent}`}  id={id} onClick={click}>

                    {icon}
                    <p> {text} </p>
                
                </button>
            </Link>
        </div>
    );
}

export default HeaderButton