import React from 'react'
import styles from '../Displayer.module.css'
import { IoSend } from 'react-icons/io5'

function CommentBox({submit}) {
    
    return (
        <div className={styles.form} >
            <form onSubmit = {submit} >
                <textarea placeholder="Escribe tu comentario" required minLength={8} className = {styles.textarea} type = "text" name = "comment" /> 
                <button type = "submit" value = "" > 
                    <IoSend className = {styles.img} alt = "" /> 
                </button> 
          </form>
        </div>
    )
}

export default CommentBox
