import React, { useState } from 'react'
import styles from '../App.module.css';
import AppHeader from "../components/Header"
import Cards from "../components/Cards"
import { IoArrowBackCircle } from 'react-icons/io5'
import Displayer from "../components/Displayer"
import { useUserContext } from '../contexts/UserContext';
import updateCard from "../services/update"
const Main = () => {

  const CARDSMODE  = "admin"
  const { width } = useUserContext()

  const [displayerState, changeDisplayer] = useState({
    show: "",
    data: ""  
  })

  const editInfo = (e) => {
    changeDisplayer({
      show: "edit",
      data: {
        id: e.target.name
      }
    })
  }

  const commentInfo = (e) => {
    changeDisplayer({
      show: "comment",
      data: {
        id: e.target.name
      }
    })
  }

  const changedData = (id, title, description, image) => {
    console.log(id, title, description, image)

    updateCard(id, title, description, image)

    changeDisplayer({
      show: "",
      data: ""
    })
  }

  const BrowserViewComponents = () => {

    if (width >= 1024 && !((width < window.innerHeight)))
      return (
        <div className={styles.container}>
          <Displayer updateData={changedData} display={displayerState} />

          <Cards
            mode={CARDSMODE}
            editButton={editInfo}
            likeButton={like}
            commentButton={commentInfo}

          />

        </div>)
      else
        return (
          <></>
        )

  }

  const MobileViewComponents = () => {
    if (width < 1024  || (width < window.innerHeight)) {
      if ((displayerState.show))
        return (
          
        <div className={styles.mobileDisplay}>
            <button className={styles.closeButton} onClick={() => {
              changeDisplayer({
                show: "",
                data: ""
              })
            }}
            >
              <IoArrowBackCircle />
              <span>Regresar</span>
            </button>
            <Displayer updateData={changedData} display={displayerState} />
          </div>
        )
      else {
        return (
          <div className={styles.cardMobile}>
           <h2 className={styles.title}><p>Mis Publicaciones</p></h2>

          <Cards
            mode={CARDSMODE}
            editButton={editInfo}
            likeButton={like}
            commentButton={commentInfo}
            
            />
            </div>
        )

      }
    }
    else
      return (
        <></>
      )
  }

  const like = () => {
    //Alejandro
  }

  return (
    <div className={styles.main}>

      <AppHeader headerOf="mine" />

      <div className={styles.container}>

        {
          <MobileViewComponents />
        }
        {
          <BrowserViewComponents />
        }
      </div>

    </div>
  )
}

export default Main