const toggle = async (id) => {

    let token = localStorage.getItem("token");

    try {
        const header = {
            method: 'PATCH',
            headers: {"Authorization": `Bearer ${token}`}
        }

        const toggle = await fetch(
            "https://posts-pw2021.herokuapp.com/api/v1/post/toggle/" + id,
            header
            )

        if(!(toggle.ok)){
            throw new Error()
        }

        //let toggle = await toggle.json()

        return true
        

    } catch (error) {

        console.log(error)

        return false
    }
}

export default toggle