const  like = async (id) => {

    let token = localStorage.getItem("token");

    try {
        const header = {
            method: 'PATCH',
            headers: {"Authorization": `Bearer ${token}`}
        }

        const like = await fetch(
            "https://posts-pw2021.herokuapp.com/api/v1/post/like/" + id,
            header
            )

        if(!(like.ok)){
            throw new Error()
        }

        let likeData = await like.json()

        return likeData
        

    } catch (error) {

        console.log(error)

        return false
    }
}

export default like