import { useState } from 'react';
import { useUserContext } from '../../contexts/UserContext';
import { Navigate } from 'react-router-dom';
import styles from './Login.module.css'
import LoginImg from '../../assets/img/login-imgGris.png'



//gp25_user
//cMJfIKzxhQF3wfEe

export default function Login() {
    const { login, token } = useUserContext();
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState(false);
    
    if (token) {
        return <Navigate replace to="/COMUNIDADUCAGRUPO25/home" />
    }

    const onChange = (e, save) => {
        save(e.target.value);
    }

    const onSubmitHandler = async (e) => {
        e.preventDefault();
        
        localStorage.setItem("credentials", JSON.stringify({
            user: username, 
            password: password
        })) 
        const logged = await login();
        setError(!logged);
        setUsername("");
        setPassword("");
    }

    return (
                <div className={styles.container}>
                    <div className={styles.main}>
                    <form onSubmit={onSubmitHandler} className={styles.form}>                
                    <div className={styles.inputs}>
                        <h1 className={styles.h1}> Log in</h1>
                        <input className={styles.username}
                                type='text'
                                value={username}
                                placeholder='username'
                                onChange={(e) => onChange(e, setUsername)}
                            />

                            <input className={styles.password}
                                type="password"
                                placeholder="password"
                                onChange={(e) => onChange(e, setPassword)}
                                value={password}
                            />
                            {error && <p className={styles.error}>Un error ha ocurrido en el inicio de sesión</p>}
                            <button className={styles.btn}>Sign In </button>
                    </div>    
                        <div className={styles.img}>
                            <img alt = "" src={LoginImg} className={styles.img}></img>
                        </div>
                    </form>
                    </div>
                </div>
    );
}