const update = async (id, title, descripcion, image) => {

    let token = localStorage.getItem("token");

    try {
        const header = {
            method: 'PUT',
            headers: {
                "Authorization": `Bearer ${token}`,
                "Content-type": "application/json"
            },
            body: JSON.stringify({
                title: title,
                description: descripcion,
                image: image
            })
        }

        const update = await fetch(
            "https://posts-pw2021.herokuapp.com/api/v1/post/update/" + id,
            header
            )

        if(!(update.ok)){
            throw new Error()
        }

       // let update = await update.json()

        return true


    } catch (error) {

        console.log(error)

        return false
    }
}

export default update