import HeaderButton from "./headerButton";
import styles from "./header.module.css";
import { MdPeopleAlt } from 'react-icons/md'
import { FaStar } from 'react-icons/fa'
import { FaRegUserCircle } from 'react-icons/fa'
import { IoLogOut } from 'react-icons/io5'
import { useUserContext } from "../../contexts/UserContext"
import { AiOutlinePlus, AiFillHome } from 'react-icons/ai'

const AppHeader = ({ headerOf }) => {

    let { user } = useUserContext();
    let texts = {
            publish: "Publicar",
            home: "Inicio",
            mine: "Mis publicaciones",
            favs: "Favoritos",
            logout: "Log Out"
        }
    


    if (user.role === "admin") {
        return (
            <div className={styles.header}>
                <div className={styles.logoContainer}>
                    <h2 className={styles.name}>
                        <FaRegUserCircle />
                        {user.username}
                     </h2>
                 </div>
                <div className={styles.container}>

                    <HeaderButton icon={<AiOutlinePlus />} id="button" active={headerOf} route="create" text={texts.publish} > </HeaderButton>

                    <HeaderButton icon={< MdPeopleAlt />} id="button" active={headerOf} route="home" text={texts.home} > </HeaderButton>

                    <HeaderButton icon={<AiFillHome />} id="button" active={headerOf} route="mine" text={texts.mine}> </HeaderButton>

                    <HeaderButton icon={<FaStar />} id="button" active={headerOf} route="favs" text={texts.favs}> </HeaderButton>

                    <HeaderButton icon={<IoLogOut />} id="button" active={headerOf} route="logout" text={texts.logout}>  </HeaderButton>
                </div>
            </div>
        );
    }
    else{
        return (
            <div className={styles.header}>
                <div className={styles.logoContainer}>
                    <h2 className={styles.name}>
                        <FaRegUserCircle />
                        {user.username}
                     </h2>
                 </div>                <div className={styles.container}>

                    <HeaderButton icon={< MdPeopleAlt />} id="button" active={headerOf} route="home" text={texts.home}> </HeaderButton>

                    <HeaderButton icon={<FaStar />} id="button" active={headerOf} route="favs" text={texts.favs}> </HeaderButton>

                    <HeaderButton icon={<IoLogOut />} id="button" active={headerOf} route="logout" text={texts.logout}>  </HeaderButton>
                </div>
            </div>
        );
    }
}

export default AppHeader

