import React, { useState, useEffect, useMemo, useCallback } from "react";
import userService from '../services/user.services';

const UserContext = React.createContext();
const TOKEN_KEY = "token";

export const UserProvider = (props) => {
    const [token, setToken] = useState(undefined);
    const [user, setUser] = useState(undefined);

useEffect(() => {
        const verifyTokenAsync = async () => {
            const lsToken = getToken();
            let {user, password} = JSON.parse(localStorage.getItem("credentials"))

            if(lsToken && user && password) {
                const { username, role } = await userService.verifyToken(lsToken);
                await login()
                if(username && role) {
                    setUser({ username, role });
            }
        }
    }
    verifyTokenAsync();
},[token])

const setTokenAll = (token) => {
    localStorage.setItem(TOKEN_KEY, token);
    setToken(token);
}

const login = useCallback(()=> {
    let {user, password} = JSON.parse(localStorage.getItem("credentials"))
        const loginAsync = async () => {
        let status = false;
        try {
            const { token: tokenRes } = await userService.login(user, password);
            if(tokenRes) {
            setTokenAll(tokenRes);
            status = true;
                }
        } catch (error) {
        } finally {
            return status;
        }
    };
        return loginAsync();
},[])

const logout = useCallback(() => {
    setUser(undefined);
    setTokenAll(undefined);
}, [])

const [width, changeWidth] = useState(window.innerWidth)

useEffect(() => {
    window.addEventListener("resize", () => {
    changeWidth(window.innerWidth)
    })
    return () => {
        window.removeEventListener("resize", () => { })
    }
},[])

const value = useMemo(()=> ({
    token: token,
    user: user,
    login: login,
    logout: logout,
    width: width, 
    changeWidth: changeWidth
}),[token, user, width, login, logout]);
    return <UserContext.Provider value={value} {...props} />;
}

export const useUserContext = () => {
    const context = React.useContext(UserContext);
    if (!context) {
        throw new Error();
    }
    return context;
}

const getToken = () => localStorage.getItem(TOKEN_KEY);
