const getAllPosts = async (token, page) => {
    try {

        //console.log(token)

        const header = {
            method: 'GET',
            headers: {"Authorization": `Bearer ${token}`}
        }

        const postsResponse = await fetch(
            "https://posts-pw2021.herokuapp.com/api/v1/post/all?limit=15&page="+page,
            header
            )

        if(!(postsResponse.ok)){
            throw new Error("")
        }
        
        const data = await postsResponse.json()
        return data

    } catch (error) {
        console.log(error)
        return {data: []}
    }
}

export default getAllPosts
