import React from 'react'
import { useEffect, useState } from "react";
import styles from './cards.module.css'
import SingleCard from './singleCard.js'
import getAllPosts from "./../../services/getAllPosts";
import getMyPosts from "./../../services/getMyposts";
import getFavsPosts from "../../services/getFavsPosts";
import getPostById from '../../services/getPostById';
import polar from './Background.png'


const Cards = ({ mode, commentButton, editButton,
    historyButton, deleteButton, likeButton }) => {

    const [currentPage, changePagesLimit] = useState(0)
    const [posts, setPosts] = useState([])
    const [favs, setFavs] = useState([])

    useEffect(() => {
        (async () => {
            let tokenStorage = localStorage.getItem("token")
            let postList = {
                data: []
            }

            console.log("mode", mode)

            if (tokenStorage !== { "Error": true }) {

                let { favorites } = await getFavsPosts(tokenStorage, 0);

                if (mode === "admin") {
                    postList = await getMyPosts(tokenStorage, 0)
                    let { data } = postList
                    setPosts(data)
                }
                else if (mode === "user") {
                    postList = await getAllPosts(tokenStorage, 0)
                    let { data } = postList
                    setPosts(data)
                }
                else if (mode === "favs") {
                    const checkPromises = favorites.reverse().map(async (element) => {
                        
                        let post = await getPostById(tokenStorage, element)
                        console.log("Post", posts)
                        let postList = posts

                        if(post.error === 'Post not found!'){
                            return
                        }

                        postList.push(post)

                        console.log(postList)
                        setPosts(postList)

                    });

                    Promise.all(checkPromises);

                }
                console.log(favorites)
                setFavs(favorites)
            }

        })()


    }, [])

    useEffect(() => {

        return () => {
            setPosts([])
            changePagesLimit(0)
            setFavs([])
        }

    }, [mode])

    useEffect(() => {

        (async () => {
            console.log("Page has changed")
            let tokenStorage = localStorage.getItem("token")
            let postList = { data: [] }

            if (mode === "admin") {
                postList = await getMyPosts(tokenStorage, currentPage)
            }
            else if (mode === "user") {
                postList = await getAllPosts(tokenStorage, currentPage)
            }


            let { data } = postList
            setPosts(posts.concat(data))

        })()

    }, [currentPage])


    const showMorePages = () => {
        changePagesLimit(currentPage + 1)
    }

    const toogleFav = (id) => {

        let favArray = []

        if(favs.includes(id)){
            favArray = favs.filter((element) => {
                return element !== id
            })
        }else{
            favArray = favs.concat([id])
        }

        setFavs(favArray)
    }
    return (
        <div className={styles.container}>

            {(() => {

                if (!posts || posts.length === 0) {
                    return (


                        <div>
                            
                            <p className = {styles.noPostText}>
                               Oh... 
                               ¿No hay nada para mostrar? 
                            </p>
                            <p className = {styles.noPostTextThin}>
                                (Interactua con la comunidad para ver posts en esta seccion)
                            </p>
                            
                            <img  alt = "no post" className = {styles.noPostImg} src={polar} />

                                <br />

                            <button className = {styles.noPostButton} onClick={() => window.location.reload()}>
                               Intenta actualizar la pagina!!
                            </button>

                        </div>

                    )

                }
                else {

                    return (

                        <div>
                            {
                                posts.map(({ title, description, image, user, likes, active, history,  createdAt, _id, comments }) => {

                                    return <SingleCard mode={mode}
                                        title={title}
                                        description={description}
                                        image={image}
                                        user={user}
                                        likes={likes}
                                        fav={favs.includes(_id)}
                                        active={active}
                                        history = {history.length}
                                        time={createdAt}
                                        id={_id}
                                        key={_id}
                                        comments={comments}
                                        commentClick={commentButton}
                                        editClick={editButton}
                                        favClick={toogleFav}
                                        historyClick={historyButton}
                                        deleteButton={deleteButton}
                                        likeClick={likeButton} />
                                })
                            }

                            <button className={styles.noPostButton} onClick={showMorePages}> Mostrar mas posts </button>
                        </div>
                    )
                }
            })()}
        </div>
    )
}

export default Cards
