const description = async (id, descripcion) => {

    let token = localStorage.getItem("token");

    try {
        const header = {
            method: 'PATCH',
            headers: {
                "Authorization": `Bearer ${token}`,
                "Content-type": "application/json"
            },
            body: JSON.stringify({
                description: descripcion
            })
        }

        const description = await fetch(
            "https://posts-pw2021.herokuapp.com/api/v1/post/comment/" + id ,
            header
            )

        if(!(description.ok)){
            throw new Error()
        }

        console.log(await description.json())

        return true


    } catch (error) {

        console.log(error)

        return false
    }
}

export default description