import Displayer from '../components/Displayer';
import React, { useState } from 'react'
import AppHeader from "../components/Header"
import SingleCard from '../components/Cards/singleCard';
import styles from '../App.module.css';
import placeholder from "../image-placeholder.png"
import { useUserContext } from '../contexts/UserContext';
import createPost from "../services/post"

export const Publish = () => {

    const { width } = useUserContext()

    const [displayerState, changeDisplayer] = useState({
      show: "create"
    })
    
    const [title, setTitle] = useState("[ASI SE VERA TU PUBLICACION]")
    const [image, setImage] = useState(placeholder)
    const [description, setDescription] = useState("[Que estas pensando?]")
    const createData = {
        user : {
                username: "gp25_admin"
        },
        title: "[ASI SE VERA TU PUBLICACION]", 
        description: "[Que estas pensando?]",
        image: placeholder,
        time: Date.now(),
        likes: [],
        comments: []
    }

    
    const changePreview = (target, value) =>{
  

      console.log(target, value)
        if(target === "title")
          setTitle(value)
        else if(target === "image")
          setImage(value)
        else if(target === "description")
          setDescription(value)
        else
          changeDisplayer({
            show: "",
            data: ""
          })

    }

    const BrowserViewComponents = () => {

      if (width >= 1024 && !((width < window.innerHeight)))
        return (
          <div className={styles.container}>
            <Displayer updateData={changePreview} display={displayerState} />
          
            <div className={styles.previewCardContiner}>

              <SingleCard
              user = {createData.user}
              title={title}
              description={description}
              image={image}
              time={createData.time}
              likes={createData.likes}
              active={true}
              comments={createData.comments}
              />
              </div>
  
          </div>)
        else
          return (
            <></>
          )
  
    }
    const MobileViewComponents = () => {
      if (width < 1024  || (width < window.innerHeight)) {
        if ((displayerState.show))
          return (
            
          <div className={styles.mobileDisplay}>

              <Displayer updateData={changePreview} display={displayerState} />
            </div>
          )
        else {
          return (
            <div className={styles.cardMobile}>
             <h2 className={styles.title}><p>Mi nueva publicacion</p></h2>
  
             <SingleCard
              user = {createData.user}
              title={title}
              description={description}
              image={image}
              time={createData.time}
              likes={createData.likes}
              active={true}
              comments={createData.comments}
              />
              </div>
          )
  
        }
      }
      else
        return (
          <></>
        )
    }
  
    return (
      <div className={styles.main}>
  
        <AppHeader headerOf="create" />
  
        <div className={styles.container}>
  
          {
            <MobileViewComponents />
          }
          {
            <BrowserViewComponents />
          }
        </div>
  
      </div>
    )

    /*return (
      <div className={styles.main}>
  
        <AppHeader headerOf="create"/>
  
        <div className={styles.container}>
          <Displayer display={displayerState}  />

          <div className={cardsStyles.container}>
            <SingleCard
                user = {createData.user}
                title={createData.title}
                description={createData.description}
                image={createData.image}
                time={createData.time}
                likes={createData.likes}
                comments={createData.comments}
            />

            </div>
  
        </div>
  
      </div>
    )*/
  }

export default Publish;


