import styles from './cards.module.css'
import { FaHistory } from "react-icons/fa"
import { useState } from 'react'
import { AiFillStar } from "react-icons/ai"
import { useUserContext } from "../../contexts/UserContext"
import doLike from '../../services/like'
import doFav from '../../services/fav'
import doActive from '../../services/togglePost'

function SingleCard({ mode, id, title, description, image, user, fav, likes, active, time, comments, history,
    commentClick, editClick, historyClick, favClick }) {

        const { user: userData } = useUserContext();
        const [likesCount, changeLike] = useState(likes)
        const [activeState, toogleActive] = useState(active)
        
    const extractGroupName = () => {
        let indexOF_ = user.username.indexOf("_")
        let substring = user.username.substring(2, indexOF_)
        return `Grupo ${substring}`
    }

    const extractDateTime = () => {
        let creation = new Date(time)
        return `${creation.toString().substring(0, 24)}`
    }

    const toogleFav = () => {
        (async() => {
            let res = await doFav(id)
            console.log(res)
        })()
        favClick(id)
    }

    const showOptionsByMode = () => {

        if (mode === "admin") {
            if (activeState) {

                return (
                    <div className={styles.editMode}>
                        <button name={id} onClick={editClick} title="Editar"> 🎨 </button>
                        <button name={id}
                            onClick={
                                () => {
                                    doActive(id)
                                    toogleActive(!activeState)
                                }}
                            title="Eliminar"> ❌ </button>
                    </div>
                )
            } else {
                return (
                    <div className={styles.editMode}>
                        <button name={id} onClick={
                            () => {
                                doActive(id)
                                toogleActive(!activeState)
                            }}
                            title="Activar"> ✅ </button>
                    </div>
                )
            }
        }
        else if (mode === "user" || mode ==="favs") {

            if (history)
                return (
                    <div className={styles.editMode}>
                        <button name={id} onClick={historyClick} title="Historial">
                            <FaHistory name={id} className={styles.history_icon} />
                        </button>

                        <button name={id} onClick={toogleFav} title="Hacer favorito">
                            <AiFillStar className={styles[`favorite_${fav}`]} />
                        </button>
                    </div>
                )
            else
                return (
                    <div className={styles.editMode}>
                        <button name={id} onClick={toogleFav} title="Hacer favorito">
                            <AiFillStar className={styles[`favorite_${fav}`]} />
                        </button>
                    </div>
                )
        }
    }

    const toogleLike = () => {
                              
        (async() => {
            await doLike(id);
        })()

        let iDidLike = likesCount.filter((element) => {
            return element.username === userData.username
        }).length
        
        let likesWithoutMe = likesCount.filter((element) => {
            return element.username !== userData.username
        })

        if(iDidLike)
            changeLike(likesWithoutMe)
        else
            changeLike(likesCount.concat([{username: userData.username}]))


    }


    const showInteractions = () => {
        if (likes && comments && activeState) {
            return (
                <div className={styles.interactions}>
                    <div className={styles.row}>
                        <button onClick={toogleLike} name={id}> 👍 </button>
                        <p title=""> {likesCount.length} Likes </p>
                    </div>
                    <div className={styles.row}>
                        <button onClick={commentClick} name={id} > 💬 </button>
                        <span> {comments.length} Comentarios </span>
                    </div>
                </div>)
        }

    }

    return (
        <div className={styles.card}>
            <div className={styles.information}>

                <p className={styles.title}>
                    {title}
                </p>

                <p className={styles.user}>
                    {extractGroupName()}
                </p>

                <p title={new Date(time)} className={styles.time}>
                    {extractDateTime()}
                </p>


                <div className={styles.history}>
                    {showOptionsByMode()}
                </div>

                <p className={styles.description}>
                    {description}
                </p>
            </div>

            <div className={styles.image}>
                <img src={image} alt="" />
            </div>

            {
                showInteractions()
            }

        </div>
    )
}

export default SingleCard
