const fav = async (id) => {

    let token = localStorage.getItem("token");

    try {
        const header = {
            method: 'PATCH',
            headers: {"Authorization": `Bearer ${token}`}
        }

        const fav = await fetch(
            "https://posts-pw2021.herokuapp.com/api/v1/post/fav/" + id,
            header
            )

        if(!(fav.ok)){
            throw new Error()
        }

        let favData = await fav.json()

        return favData
        

    } catch (error) {

        console.log(error)

        return false
    }
}

export default fav